:root{


    // Primary
    --dark-cyan: hsl(185, 75%, 39%);
    --dark-cyan-transparent: hsla(185, 75%, 39%, 0.257);
    --dark-blue-desaturated: hsl(229, 23%, 23%);
    --dark-grayish-blue: hsl(227, 10%, 46%);

    // Neutral
    --dark-gray: hsl(0, 0%, 59%);
    --light-gray: hsl(0, 0%, 91%);
    --white: hsl(0, 0%, 100%);


}