html {
  box-sizing: border-box;
  font-size: 100%;
}

*,
*::after,
*::before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Kumbh Sans", sans-serif;
  background-color: var(--dark-cyan);
  color: var(--dark-blue-desaturated);
  background-image: url("./../../images/bg-pattern-top.svg"), url("./../../images/bg-pattern-bottom.svg");
  background-position: bottom 50vh right 37vw, top 50vh left 37vw;
  background-repeat: no-repeat, no-repeat;
}

:root {
  --dark-cyan: hsl(185, 75%, 39%);
  --dark-cyan-transparent: hsla(185, 75%, 39%, 0.257);
  --dark-blue-desaturated: hsl(229, 23%, 23%);
  --dark-grayish-blue: hsl(227, 10%, 46%);
  --dark-gray: hsl(0, 0%, 59%);
  --light-gray: hsl(0, 0%, 91%);
  --white: hsl(0, 0%, 100%);
}

h3 {
  font-size: 0.9375rem;
  margin-bottom: 0.9375rem;
}

.grid {
  display: flex;
  justify-content: center;
  align-items: center;
}

.container {
  padding: 0 1.5rem;
  margin: auto;
}
@media (min-width: 71.875em) {
  .container {
    max-width: 69.375rem;
    padding: 0;
  }
}

.h-full {
  min-height: 100vh;
}

.card {
  width: 20.375rem;
  background-color: var(--white);
  border-radius: 0.875rem;
  overflow: hidden;
  box-shadow: 0 0.625rem 2.1875rem 0.625rem var(--dark-cyan-transparent);
}
.card__bg {
  padding: 4.6875rem;
  background-size: cover;
  z-index: -1;
}
.card__body {
  padding: 1.5625rem;
  position: relative;
  width: 100%;
  top: -4.6rem;
  margin-bottom: -4.6rem;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border-bottom: 0.0625rem solid var(--light-gray);
}
.card__image {
  width: 6rem;
  height: 6rem;
  border-radius: 50%;
  overflow: hidden;
  border: 0.375rem solid var(--white);
  margin-bottom: 0.9375rem;
}
.card__image img {
  width: 100%;
  object-fit: cover;
}
.card__username--light {
  font-weight: 400;
  font-size: 0.875rem;
  color: var(--dark-grayish-blue);
}
.card__location {
  font-size: 0.726875rem;
  color: var(--dark-grayish-blue);
}
.card__footer {
  padding: 1.5625rem 2.625rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: center;
}
.card__count--value {
  font-weight: 700;
  font-size: 0.9375rem;
}
.card__count--desc {
  color: var(--dark-gray);
  font-size: 0.625rem;
  letter-spacing: 0.0625rem;
}

.footer {
  text-align: center;
}
.footer__attribution {
  padding: 0.625rem;
  background: var(--dark-blue-desaturated);
  border-top-right-radius: 2.5rem;
  border-top-left-radius: 2.5rem;
  color: var(--white);
}
.footer__attribution a {
  color: var(--dark-cyan);
  transition: color 350ms ease;
}
.footer__attribution a:hover {
  color: #1fcbdb;
}
.footer__attribution a:focus, .footer__attribution a:active {
  color: var(--dark-cyan);
}